.accordion {
  margin: 0 auto;
  width: 100%; }
  .accordion .title {
    padding: 30px 30px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    color: #1c2a48 50%;
    position: relative;
    font-size: 20px;
    background: #ffc75f 60%;
    margin-bottom: -1px;
    border-bottom: 2px solid #1c2a48;
    text-align: left; }
    .accordion .title::after {
      content: "+";
      font-size: 18px;
      color: #ffc75f 60%;
      transition: transform 0.5s ease-in-out;
      position: absolute;
      right: 30px;
      font-family: monospace; }
    .accordion .title.is-expanded {
      transition: background 0.5s;
      background: #ffc75f 60%; }
      .accordion .title.is-expanded::after {
        content: "-";
        transform: rotate(-360deg); }
  .accordion .content {
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.5s;
    margin: 0;
    padding: 0 30px;
    border: solid 1px #1c2a48 50%;
    border-top: 0;
    background: white; }
    .accordion .content p {
      padding: 30px 0;
      margin: 0;
      opacity: 0;
      transition: 0.5s; }
    .accordion .content.is-expanded {
      max-height: 500px;
      overflow: hidden; }
      .accordion .content.is-expanded p {
        opacity: 1; }
  .accordion:after {
    width: 100%;
    height: 12px;
    display: block;
    background: #1c2a48 50%;
    content: ""; }

